.buttonsWrapper {
    margin-left: 5px;
    display: inline;
    position: relative;
    z-index: 99;
}

.hideButtonsWrapper {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    z-index: 100;
}